@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap');

:root {
  --bg-color: #0a0a0a;
  --text-color: #ffffff;
  --accent-color: #3a86ff;
  --secondary-color: #ff006e;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(135deg, var(--bg-color) 0%, #1a1a2e 100%);
  color: var(--text-color);
  line-height: 1.6;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4rem;
}
:root {
  /* ... other color variables ... */
  --dark-yellow: #ffd700;
  --red: #ff0000;
}

h1 {
  font-size: 3.5rem; /* Increased font size */
  font-weight: 700; /* Bold weight */
  letter-spacing: 2px;
  background: linear-gradient(45deg, var(--dark-yellow), var(--red));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  margin-bottom: 1rem;
  text-transform: uppercase;
}

/* Add this for better responsiveness */
@media (max-width: 768px) {
  h1 {
    font-size: 2.5rem;
  }
}




nav ul {
  display: flex;
  list-style: none;
}

nav ul li {
  margin-left: 2rem;
}

nav ul li a {
  color: var(--text-color);
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  transition: color 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
}

nav ul li a:hover {
  color: var(--accent-color);
}

section {
  margin-bottom: 6rem;
}

h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  font-weight: 700;
  background: linear-gradient(45deg, var(--accent-color), var(--secondary-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  -text-fill-color: transparent;
  letter-spacing: 1px;
}


p {
  font-size: 1.1rem;
  max-width: 600px;
  font-weight: 300;
  line-height: 1.8;
}
/* Skills Section */
#skills {
  padding: 2rem 0;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.skill-category {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 1.5rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skill-category:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.skill-category h3 {
  color: var(--accent-color);
  margin-bottom: 1rem;
  font-size: 1.2rem;
  border-bottom: 2px solid var(--accent-color);
  padding-bottom: 0.5rem;
}

.skill-category ul {
  list-style-type: none;
  padding: 0;
}

.skill-category li {
  margin-bottom: 0.5rem;
  position: relative;
  padding-left: 1.5rem;
}

.skill-category li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: var(--accent-color);
}

@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }
}

.project {
  margin-bottom: 2rem;
}

.project h3 {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

#contact {
  text-align: center;
}

#contact h2 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

#contact p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.social-links a {
  color: var(--text-color);
  font-size: 2.5rem;
  transition: all 0.3s ease;
}

.social-links a:hover {
  color: var(--accent-color);
  transform: translateY(-5px);
}

@media (max-width: 768px) {
  header {
    flex-direction: column;
    align-items: flex-start;
  }

  nav ul {
    margin-top: 1rem;
  }

  nav ul li {
    margin-left: 0;
    margin-right: 1rem;
  }

  .skills-grid {
    grid-template-columns: 1fr;
  }
}

/* Flying Arrow Animation */
@keyframes flyIn {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  50% {
    transform: translateX(10%);
    opacity: 0.5;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Apply this animation when a section is active */
.fly-in-arrow {
  animation: flyIn 1s ease forwards;
  position: relative;
}

.fly-in-arrow:before {
  content: '➤'; /* Arrow symbol */
  position: absolute;
  left: -25px;
  top: 0;
  font-size: 1.5rem;
  color: var(--accent-color);
  opacity: 0;
}

.fly-in-arrow-active:before {
  opacity: 1;
  animation: flyIn 1s ease forwards;
}
.btn {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.btn-upload {
  background-color: var(--accent-color);
  color: white;
}

.btn-upload:hover {
  background-color: #2a75e6;
}

.btn-delete {
  background-color: #ff4136;
  color: white;
  margin-left: 10px;
}

.btn-delete:hover {
  background-color: #e60000;
}

.error {
  color: #ff4136;
  margin-top: 10px;
}
/* Courses Section */
.courses-list {
  list-style-type: none;
  padding: 0;
}

.courses-list li {
  background-color: rgba(255, 255, 255, 0.1);
  margin-bottom: 10px;
  padding: 15px;
  border-radius: 5px;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.courses-list li:hover {
  transform: translateX(10px);
  background-color: rgba(255, 255, 255, 0.2);
}

/* Resume Section */
.resume-download {
  margin-top: 1rem;
}

.btn-download {
  display: inline-block;
  padding: 10px 20px;
  background-color: var(--accent-color);
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.btn-download:hover {
  background-color: #2a75e6;
}

/* Articles Section */
.article-list {
  list-style-type: none;
  padding: 0;
}

.article-list li {
  margin-bottom: 1rem;
}

.article-list a {
  color: var(--text-color);
  text-decoration: none;
  transition: color 0.3s ease;
}

.article-list a:hover {
  color: var(--accent-color);
}